@import url(https://fonts.googleapis.com/css?family=Mansalva&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    font-family: 'Goudy Old Style';
    line-height: 180%;
}

@media screen and (max-width: 600px) {
  .App {
    font-family: 'Goudy Old Style';
    line-height: 180%;
  }
}

.poem {
	padding-top: 80px;
    padding-left: 180px;
}

@media screen and (max-width: 600px) {
  .poem {
    padding-top: 40px;
    padding-left: 90px;
  }
 }
 
.poemContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.PenelopesShroud {
    margin: 40px;
}

.finalCycleSpan {
    background-color: crimson;
    color: azure;
}


.text-button {
    border: none;
    padding: 3px 0;
    background-color: inherit;
    font-family: inherit;
    font-size: inherit;
    cursor: pointer;
    display: inline-block;
}

.text-button:hover {
    background: #eee;
}

.poem2 {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.cycleSpan{
  background-color: lightgrey;
  border-radius: 10px;
  padding-left: 4px;
  padding-right: 4px;
  color: rgba(100,100,100,0.9);
}

.cycleSpan:hover{
  background-color: grey;
  color: white;
}

.link {
	color: red;
	text-decoration: none;
}

.setPenelope .cycleSpan{
	background-color: white;
	text-decoration: underline;
	-webkit-text-decoration-color: rgba(235,0,42,0.6);
	        text-decoration-color: rgba(235,0,42,0.6);
}

.setPenelope .cycleSpan:hover{
	background-color: rgba(255,220,225,0.3);
	color: black;
	text-decoration: none;
}

.setPenelope .hovertext {
	border-bottom-style: dotted;
  	border-bottom-color: rgba(235,0,42,0.6);
  	border-bottom-width: 2px;
}

.setOdysseus .cycleSpan{
	background-color: rgba(170,200,255,0.2);
	color: rgba(100,100,100,0.9);
}

.setOdysseus .cycleSpan:hover{
	background-color: rgba(0,130,250,0.6);
	color: white;
}

